<template>
  <el-button size="mini" @click="dialogVisible = true">编辑</el-button>
  <el-dialog class="edit-dialog" v-model="dialogVisible" :show-close="false" title="请输入要添加的类名" :width="300" :before-close="cancel" >
    <el-input v-model="text" maxlength="15" minlength="2" placeholder="不能超过15个字符！！！" show-word-limit type="text"></el-input>
    <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancel" size="mini">取消</el-button>
          <el-button type="primary" @click="enter">增加</el-button>
        </span>
    </template>
  </el-dialog>
</template>

<script>
export default {
  name: "EditDialog",
  data:()=>{return{
    dialogVisible:false,
    text:''
  }},
  props:['scope'],
  methods: {
    clickRefresh() { this.$emit('refresh') },
    cancel() { this.dialogVisible = false; this.text = '' },
    enter() {
      if (this.text.length < 2||this.text.length > 15) this.$message({message:'类名不能小于两个字符，不能大于15个字符!',type:'error'});
      else this.clickCreate()
    }
  }
}
</script>

<style scoped>
>>>  {
  text-align: left;
}
.edit-dialog >>> .el-dialog__header,>>> .el-dialog__body {
  padding: 15px;
  text-align: left;
}
.el-button {
  margin-right: 10px;
}
</style>