<template>
  <div class="box">
    <el-table class="box-talbe-main"
              :data="tableData.filter((data) =>!search || data.paramName.toLowerCase().includes(search.toLowerCase()))"
              max-height="450">
      <el-table-column type="index" />
      <el-table-column prop="name" label="参数名称" align="center" width="130" />
      <el-table-column prop="create_time" label="创建时间" align="center" width="110" />
      <el-table-column prop="state" label="是否可用" align="center" width="110">
        <template #default="scope">
          <el-icon color="#67C23A" v-if="scope.row.state === 1"><check /></el-icon>
          <el-icon color="#F56C6C" v-else><minus /></el-icon>
        </template>
      </el-table-column>
      <el-table-column align="right">
        <template #header>
          <el-input v-model="search" size="mini" placeholder="输入参数名称进行筛选" />
        </template>
        <template #default="scope">
          <el-button size="mini" @click="ClickEdit(scope)">编辑</el-button>
          <el-button v-if="scope.row.state === 1" type="info" size="mini" @click="ClickStopUsed(scope)">禁用</el-button>
          <el-button v-else type="primary" size="mini" @click="ClickStartUsed(scope)">启用</el-button>
          <el-button size="mini" type="danger" @click="ClickDelete(scope)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog class="edit-dialog" v-if="dialogVisible" v-model="dialogVisible" :show-close="false" title="请输入要添加的类名" :width="300" :before-close="cancel" >
      <el-input v-model="text" maxlength="15" minlength="2" placeholder="不能超过15个字符！！！" show-word-limit type="text"></el-input>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="acancel" size="mini">取消</el-button>
          <el-button type="primary" @click="EditEnter">增加</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { Check,Minus } from '@element-plus/icons'
import EditDialog from "@/views/Parameter/ParList/EditDialog";
import {SetParameter} from "@/netword/parameter";

export default {
  name: "Main",
  data: ()=>{return {search: '', dialogVisible:false, text:'', id:''}},
  computed: {
    info() {return this.$store.state.systemInfo.pruductParameter[this.item]},
    tableData() {return this.info.data},
    cls() {return this.info.cls},
    reqData() {return {
      cls:this.cls,
      text:this.text,
      id:this.id,
      handle:4
    }}
  },
  props:{item:String},
  methods:{
    ClickStopUsed(scope) {
      this.id = scope.row.id
      this.$store.dispatch('SetProductParameter',this.reqData)
    },
    ClickStartUsed(scope) {
      this.id = scope.row.id
      this.$store.dispatch('SetProductParameter',this.reqData)
    },
    ClickDelete(scope) {
      this.id = scope.row.id
      this.$store.dispatch('SetProductParameter',this.reqData)
    },
    ClickEdit(scope){
      this.dialogVisible=true;
      this.id=scope.row.id
    },
    EditEnter() {
      if (this.text.length < 2||this.text.length > 15) this.$message({message:'类名不能小于两个字符，不能大于15个字符!',type:'error'});
      else this.$store.dispatch('SetProductParameter',this.reqData);
    },
    acancel() { self.dialogVisible=false }
  },
  components:{
    Check,Minus,EditDialog
  }
}
</script>

<style lang="scss" scoped>
.box {
  height:450px
}
.box-talbe-main {
  width: 100%;
}
</style>