<template>
  <div class="card-header">
    <span>{{ title }}</span>
    <div>
      <el-button class="button" type="text" @click="clickRefresh"><el-icon><refresh /></el-icon></el-button>
      <el-button class="button" type="text" @click="dialogVisible=true"><el-icon><plus /></el-icon></el-button>
    </div>
    <el-dialog v-model="dialogVisible" :show-close="false" title="请输入要添加的类名" :width="300" :before-close="cancel" >
      <el-input v-model="text" maxlength="15" minlength="2" placeholder="不能超过15个字符！！！" show-word-limit type="text"></el-input>
      <template #footer>
          <span class="dialog-footer">
            <el-button @click="cancel" size="mini">取消</el-button>
            <el-button type="primary" @click="enter">增加</el-button>
          </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { Refresh,Plus } from '@element-plus/icons'
export default {
  name: "Header",
  data:()=>{return{ dialogVisible:false, text:'' }},
  computed:{ reqData(){return {cls:this.item, text:this.text}} },
  props:{title:{type:String,default:'没有标题'},item:{type:String,required:true}},
  components: { Refresh,Plus },
  methods: {
    clickRefresh() { this.$store.dispatch('syncGetProductParameter',this.reqData) },
    cancel() { this.dialogVisible = false; this.text = '' },
    enter() {
      if (this.text.length < 2||this.text.length > 15) {
        this.$message({message:'类名不能小于两个字符，不能大于15个字符!',type:'error'})
        this.cancel()
      } else { this.$store.dispatch('syncCreateProductParameter',this.reqData) }
    }
  }
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/deep/ .el-icon {
  font-size: 20px;
  margin-left: 20px;
}
/deep/ .el-dialog {
  text-align: left;
}
/deep/ .el-dialog__header,/deep/ .el-dialog__body {
  padding: 15px;
}
</style>